@import "@styles/vars.scss";

.section-product {
    padding: 50px 0;

    @media only screen and (max-width: $bp-lg) {
        padding: 32px 0;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 24px 0;
    }

    .container{
        max-width: 1428px;
    }

    &__inner {
    }

    .product{
        margin-bottom: 48px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 40px;
        }
    }

    &__more {
    }

    &__title {
        margin-bottom: 24px;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -12px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0 -8px;
        }

        @media only screen and (max-width: $bp-sm) {
            margin: 0;
        }

        .card-product{
            width: calc(25% - 24px);
            margin: 0 12px 32px;

            @media only screen and (max-width: $bp-xl) {
                width: calc(33.33% - 24px);
            }

            @media only screen and (max-width: $bp-lg) {
                width: calc(50% - 24px);
                margin: 0 8px 24px;
            }

            @media only screen and (max-width: $bp-sm) {
                width: 100%;
                margin: 0 0 24px;
            }
        }
    }
}
