    &__main {
        display: flex;
        flex-direction: column;
        color: #fff;
    }

    &__main-title {
        display: block;
        position: relative;
        margin-bottom: 56px;
        font-size: 40px;

        @media only screen and (max-width: $bp-xl){
            display: none;
        }

        span{
            display: inline-flex;
            align-items: center;
            justify-content: center;
            height: 56px;
            padding: 0 40px;
            font-size: 35px;
            font-family: 'Inter';
            color: #fff;
            white-space: nowrap;
            background-color: #F095BA;
            border-radius: 50px;
            margin-top: -20px;
            position: absolute;
            left: 50%;
            top: 100%;
            transform: translateX(-50%) rotate(-7deg);

            @media only screen and (max-width: $bp-xxl){
                margin-top: -5px;
            }
        }
    }

    &__subtitle {
        margin-top: auto;
        display: flex;
        align-items: center;
        color: #80A7CA;
        fill: #80A7CA;

        .icon{
            width: 45px;
            height: 45px;
            margin-right: 18px;
            flex-shrink: 0;

            @media only screen and (max-width: $bp-lg){
                height: 40px;
                width: 40px;
                margin-right: 15px;
            }
        }

        span{
            line-height: 150%;
        }

        @media only screen and (max-width: $bp-lg){
            margin: 0 auto;
            padding: 0 20px;
        }
    }

    &__block {
        margin-top: -115px;
        box-sizing: border-box;
        display: block;
        width: 615px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl){
            margin-top: 0;
            width: 100%;
            order: -1;
            margin-bottom: 32px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 20px;
        }

        form{
            width: 100%;
            box-sizing: border-box;
            display: flex;
            width: 615px;
            min-height: 354px;
            flex-shrink: 0;
            background-color: $blue;
            padding: 30px 25px 25px 42px;
            border-radius: 20px;
    
            @media only screen and (max-width: $bp-xl){
                margin-top: 0;
                width: 100%;
                order: -1;
                margin-bottom: 32px;
            }
    
            @media only screen and (max-width: $bp-lg){
                padding: 32px 28px 30px;
                margin-bottom: 20px;
                flex-direction: column;
            }
        }
    }

    &__block-form {
        width: 270px;
        flex-shrink: 0;
        margin-right: 45px;

        @media only screen and (max-width: $bp-xl){
            width: 50%;
        }

        @media only screen and (max-width: $bp-lg){
            width: 100%;
            margin-right: 0;
            margin-bottom: 50px;
        }

    }

    &__block-title {
        margin-bottom: 30px;
        color: #fff;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 30px;
        }
    }

    &__input {
        margin-bottom: 45px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__block-info {
        flex-grow: 1;
        display: flex;
        flex-direction: column;

        @media only screen and (max-width: $bp-xl){
            align-items: flex-start;
        }
    }

    &__block-item{
        margin-left: 10px;

        // &.is-open{
        //     display: block;
        //     animation: fadeInTop 0.5s ease forwards;
        // }
    }

    &__radio-tumbler {
        margin-bottom: 25px;
        height: 50px;
        width: 100%;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 35px;
            font-size: 14px;
        }
    }

    &__price-block {
        width: 100%;
    }

    &__price-old {
        display: inline-flex;
        font-family: 'Inter';
        font-size: 25px;
        line-height: 120%;
        font-weight: 700;
        color: $blue-dark;
        position: relative;

        &::after{
            content:"";
            width: 100%;
            background-color: #F095BA;
            height: 4px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%) rotate(-12deg);
        }
    }

    &__price {
        font-family: 'Inter';
        font-size: 40px;
        line-height: 120%;
        font-weight: 700;
        margin-bottom: 5px;
        color: #fff;
        margin-left: 5px;
    }

    &__block-text{
        color: #fff;
        opacity: 0.5;
        margin-bottom: 55px;
        margin-left: 5px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 45px;
        }
    }

    &__button {
        margin-top: auto;
        width: 100%;
    }
}