@import '@styles/vars.scss';

.card-teacher {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #262626;
    padding: 12px 16px;
    background-color: transparent;
    border-radius: 16px;
    transition: background-color 0.35s ease;

    &:hover{
        background-color: #fff;
    }
    
    @media only screen and (max-width: $bp-lg) {
        border-radius: 0;
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        overflow: hidden;
        flex-shrink: 0;
        margin-right: 15px;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__inner {
        display: flex;
        flex-direction: column;
        justify-content: center;

        h4{
            font-family: 'Raleway';
            font-weight: 600;
            font-size: 16px;
            line-height: 120%;

            @media only screen and (max-width: $bp-lg) {
                font-weight: 500;
            }
        }

        p{
            font-family: 'Raleway';
            font-weight: 500;
            font-size: 16px;
            line-height: 120%;
            color: #BFBFBF;
            margin-top: 4px;
        }
    }

    &__text{
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        p{
            display: inline-flex;
            margin-right: 6px;

            &:last-child{
                margin-right: 0;
            }
        }
    }
}