@import "@styles/vars.scss";

.section-payment {
    padding: 60px 0 54px;


    @media only screen and (max-width: $bp-lg){
        padding: 40px 0;
    }

    &__inner{
        display: flex;
        align-items: flex-start;
    }

    &__content {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__header{
        margin-bottom: 50px;

        @media only screen and (max-width: $bp-xl){
            margin-bottom: 36px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 28px;
        }
    }

    &__title {

        @media only screen and (max-width: $bp-md){
            font-size: 30px;
        }
    }

    &__body {
        margin-bottom: 50px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 40px;
        }
    }

    &__items {
    }

    &__item {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #fff;
        border-radius: 20px;
        padding: 24px 32px 32px 40px;
        min-height: 108px;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg){
            padding: 22px 18px 20px 20px;
            min-height: auto;
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md){
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 16px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__item-title {
        max-width: 366px;

        @media only screen and (max-width: $bp-lg){
            max-width: none;
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 24px;
        }
    }

    &__item-price {
        display: inline-flex;
        align-items: center;
        font-weight: 500;
        font-size: 40px;
        line-height: 120%;
        flex-shrink: 0;
        margin-left: 32px;

        @media only screen and (max-width: $bp-md){
            margin-left: auto;
        }
    }

    &__footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-top: 1px solid #E7E7E7;
        padding-top: 32px;

        @media only screen and (max-width: $bp-md){
            padding-top: 0;
            border-top: none;
            flex-direction: column;
        }
    }

    &__footer-text {

        @media only screen and (max-width: $bp-md){
            text-align: center;
        }
    }

    &__item-button {
        width: 100%;
        max-width: 254px;
        height: 60px;
        font-size: 20px;

        @media only screen and (max-width: $bp-md){
            order: -1;
            margin-bottom: 28px;
        }
    }

    &__img-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 366px;
        height: auto;
        flex-shrink: 0;
        margin-left: 130px;

        @media only screen and (max-width: $bp-xxl){
            width: 320px;
            margin-left: 80px;            
        }

        @media only screen and (max-width: $bp-xl){
            display: none;
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}