@import "@styles/vars.scss";

.breadcrumbs {
    display: flex;
    max-width: 100%;
    padding: 58px 0 10px;

    @media only screen and (max-width: $bp-xl) {
        padding-top: 42px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding-top: 34px;
    }

    .container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 1428px;
    }

    .cart-info{
        margin-left: 16px;
        order: 2;

        &>span{
            @media only screen and (max-width: $bp-lg) {
                display: none;
            }
        }
    }

    .cart-info + &__list{
        margin-right: 0;
    }

    &__list {
        display: flex;
        align-items: center;
        overflow-x: auto;

        @media only screen and (max-width: $bp-xl) {
            padding: 0;
        }
            
        @media only screen and (max-width: $bp-lg) {
            padding: 0 16px;
            margin: 0 -16px;
        }


        -webkit-tap-highlight-color: transparent;
        scrollbar-color: transparent transparent;
        scrollbar-width: thin;
        
        &::-webkit-scrollbar {
            height: 5px;
            width: 5px;
        }
        &::-webkit-scrollbar-track {
            background-color: transparent;
        }
        &::-webkit-scrollbar-thumb {
            background-color: transparent;
        }
    }

    &__link {
        display: inline-flex;
        align-items: center;
        flex-shrink: 0;
        color: $blue;
        text-decoration: none;
        white-space: nowrap;
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.2px;
        transition: color 0.35s ease;

        &::after{
            content: '';
            display: inline-flex;
            width: 9px;
            height: 16px;
            flex-shrink: 0;
            margin: 0 16px;
            background-color: #BFBFBF;
            mask-size: contain;
            mask-repeat: no-repeat;
            mask-image: url("data:image/svg+xml,%3Csvg width='9' height='16' viewBox='0 0 9 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.180771 0.180771C0.237928 0.123469 0.305828 0.0780065 0.380583 0.0469869C0.455337 0.0159672 0.535477 0 0.616412 0C0.697347 0 0.777487 0.0159672 0.852241 0.0469869C0.926996 0.0780065 0.994896 0.123469 1.05205 0.180771L8.4358 7.56452C8.4931 7.62168 8.53857 7.68958 8.56959 7.76433C8.60061 7.83909 8.61657 7.91923 8.61657 8.00016C8.61657 8.0811 8.60061 8.16124 8.56959 8.23599C8.53857 8.31074 8.4931 8.37865 8.4358 8.4358L1.05205 15.8196C0.936514 15.9351 0.779809 16 0.616412 16C0.453015 16 0.29631 15.9351 0.180771 15.8196C0.0652316 15.704 0.000322157 15.5473 0.000322157 15.3839C0.000322157 15.2205 0.0652316 15.0638 0.180771 14.9483L7.13011 8.00016L0.180771 1.05205C0.123469 0.994897 0.078006 0.926996 0.0469863 0.852242C0.0159666 0.777487 0 0.697347 0 0.616412C0 0.535478 0.0159666 0.455338 0.0469863 0.380583C0.078006 0.305829 0.123469 0.237928 0.180771 0.180771V0.180771Z' fill='%23BFBFBF'/%3E%3C/svg%3E%0A");
            -webkit-mask-image: url("data:image/svg+xml,%3Csvg width='9' height='16' viewBox='0 0 9 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.180771 0.180771C0.237928 0.123469 0.305828 0.0780065 0.380583 0.0469869C0.455337 0.0159672 0.535477 0 0.616412 0C0.697347 0 0.777487 0.0159672 0.852241 0.0469869C0.926996 0.0780065 0.994896 0.123469 1.05205 0.180771L8.4358 7.56452C8.4931 7.62168 8.53857 7.68958 8.56959 7.76433C8.60061 7.83909 8.61657 7.91923 8.61657 8.00016C8.61657 8.0811 8.60061 8.16124 8.56959 8.23599C8.53857 8.31074 8.4931 8.37865 8.4358 8.4358L1.05205 15.8196C0.936514 15.9351 0.779809 16 0.616412 16C0.453015 16 0.29631 15.9351 0.180771 15.8196C0.0652316 15.704 0.000322157 15.5473 0.000322157 15.3839C0.000322157 15.2205 0.0652316 15.0638 0.180771 14.9483L7.13011 8.00016L0.180771 1.05205C0.123469 0.994897 0.078006 0.926996 0.0469863 0.852242C0.0159666 0.777487 0 0.697347 0 0.616412C0 0.535478 0.0159666 0.455338 0.0469863 0.380583C0.078006 0.305829 0.123469 0.237928 0.180771 0.180771V0.180771Z' fill='%23BFBFBF'/%3E%3C/svg%3E%0A");
            -webkit-mask-size: contain;
            transition: background-color 0.35s ease;
        }

        &:hover{
            color: $blue-dark;

            &::after{
                background-color: $blue-dark;
            }
        }

        &:last-of-type{
            color: #BFBFBF;
            pointer-events: none;

            &::after{
                display: none;
            }
        }
    }
}

.breadcrumbs + section{
    padding-top: 32px;

    @media only screen and (max-width: $bp-lg) {
        padding-top: 24px;
    }
}