@import "@styles/vars.scss";

.section-search{
    padding: 40px 0;

    @media only screen and (max-width: $bp-lg) {
        padding-top: 24px;
    }

    &__inner {
        max-width: 800px;
        margin: 0 auto;

        @media only screen and (max-width: $bp-xl) {
            max-width: none;
        }
    }

    &__list{
        display: flex;
        flex-direction: column;

     
    }

    .search-block{
        padding: 24px 0;
        border-top: 1px solid #BFBFBF;

        @media only screen and (max-width: $bp-lg) {
            border-top: 0;
            padding: 16px 0;
        }

        &__list{
            @media only screen and (max-width: $bp-lg) {
                margin: 0 -20px;
                width: calc(100% + 40px);
            }

            @media only screen and (max-width: $bp-md) {
                margin: 0 -16px;
                width: calc(100% + 32px);
            }
        }

        &:first-child{
            border-top: 0;
            padding-top: 0;
        }
    }

    &__title {
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 40px;
        line-height: 110%;
        margin-bottom: 24px;
    }

    &__input {
        margin-bottom: 24px;
    }

    &__nothing {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        width: 100%;
        padding: 24px 0;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            padding-top: 8px;
        }
    }

    &__nothing-img{
        width: 260px;
        height: 260px;
        flex-shrink: 0;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            width: 220px;
            height: 220px;
        }
    }

    &__nothing-title{
        display: inline;
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 32px;
        line-height: 110%;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 28px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 24px;
        }

        span{
            display: inline;
        }

        img{
            display: inline;
            vertical-align: middle;
            width: 32px;
            height: 42px;
            flex-shrink: 0;
            margin-left: 5px;
            object-fit: contain;

            @media only screen and (max-width: $bp-lg) {
                width: 24px;
                height: 32px;
            }
        }
    }

    &__nothing-text{
        color: #595959;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
        }
    }

    &__recommendations{

    }

    &__recommendations .search-block__title{
        font-size: 18px;
        line-height: 130%;
        margin-bottom: 24px;
    }
}