@import "@styles/vars.scss";

.input-number {
    display: flex;
    align-items: center;

    &__button {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        fill: #fff;
        background-color: rgba(58, 179, 254, 0.5);
        transition: background-color 0.35s ease, opacity 0.35s ease;

        .icon{
            width: 16px;
            height: 16px;
        }

        &:hover{
            background-color: $blue;
        }

        &.is-disabled{
            pointer-events: none;
            opacity: 0.35;
        }
    }

    &__field {
        box-sizing: border-box;
        width: auto;
        min-width: auto;
        margin: 0 3px;
        border: 0;
        outline: 0;
        padding: 0;
        font-family: 'Raleway';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 100%;
        color: $blue;
        text-align: center;
        background-color: transparent;
        color: #262626;
    }
}