@import '@styles/vars.scss';

.section-products {
    padding: 50px 0;

    @media only screen and (max-width: $bp-lg) {
        padding: 32px 0;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 24px 0;
    }

    &__container {
        max-width: 1428px;
    }

    &__inner {
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        padding-bottom: 24px;
        margin-bottom: 24px;
        border-bottom: 1px solid rgba(58, 179, 254, 0.3);

        @media only screen and (max-width: $bp-lg) {
            padding-bottom: 20px;
            margin-bottom: 20px;
        }
    }

    &__title {
    }

    &__filter {

        form{
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            margin: 0 -12px 2px;

            @media only screen and (max-width: $bp-xl) {
                margin-bottom: 0;
            }
    
            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 16px;
            }

            &>*{
                margin: 0 12px 30px;
    
                @media only screen and (max-width: $bp-xl) {
                    margin-bottom: 24px;
                }
    
                @media only screen and (max-width: $bp-lg) {
                    margin-bottom: 16px;
                }
            }
        }


        .tags{
            width: 60%;

            @media only screen and (max-width: $bp-xxl) {
                width: 100%;
            }
        }

        .search-input{
            max-width: 496px;

            @media only screen and (max-width: $bp-lg) {
                max-width: none;
            }
        }

        .select-links{

            &__dropdown{
                right: auto;
                left: 0;
            }
        }
    }

    &__content {
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -12px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0 -8px;
        }

        @media only screen and (max-width: $bp-sm) {
            margin: 0;
        }

        .card-product{
            width: calc(25% - 24px);
            margin: 0 12px 32px;

            @media only screen and (max-width: $bp-xl) {
                width: calc(33.33% - 24px);
            }

            @media only screen and (max-width: $bp-lg) {
                width: calc(50% - 24px);
                margin: 0 8px 24px;
            }

            @media only screen and (max-width: $bp-sm) {
                width: 100%;
                margin: 0 0 24px;
            }
        }
    }

    &__pagination {
    }
}