@import '@styles/vars.scss';


.cart-info{
    display: flex;
    flex-shrink: 0;

    &>span{
        display: flex;
        align-items: center;
        font-family: 'Raleway';
        font-weight: 600;
        font-size: 20px;
        line-height: 100%;
        padding-left: 16px;
        border-left: 1px solid #0e0b0b;
        flex-shrink: 0;
    }

    &__button {
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        margin-right: 16px;
        flex-shrink: 0;

        .icon{
            width: 100%;
            height: 100%;
        }

        span{
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 0;
            right: 0;
            transform: translate(50%, -50%);
            border-radius: 50px;
            padding: 0 5px;
            height: 16px;
            background-color: $blue;
            color: #fff;
            font-family: 'Raleway';
            font-weight: 700;
            font-size: 10px;
            line-height: 100%;
        }
    }
}