@import "@styles/vars.scss";

.card-product {
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    font-family: 'Raleway';
    color: #000000;
    border-radius: 16px;
    overflow: hidden;

    &__slider {
        position: relative;
        display: block;
        width: 100%;
        height: 205px;

        .slider {

            &__inner {
                height: 100%;
            }
        
            &__wrapper {
                height: 100%;
            }
        
            &__slide {
                height: 100%;
            }
        
            &__pagination {
                box-sizing: border-box;
                position: absolute;
                bottom: 0;
                z-index: 2;
                width: 100%;
                padding: 0 8px 8px;
            }

            &__pagination-item{
                cursor: pointer;
                box-sizing: border-box;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-grow: 1;
                border-radius: 0;
                background-color: transparent;
                padding: 8px 4px;
                margin: 0;

                &::after{
                    content: "";
                    display: block;
                    width: 100%;
                    height: 2px;
                    border-radius: 2px;
                    background-color: #BFBFBF;
                    opacity: 0.5;
                    transition: background-color 0.35s ease, opacity 0.35s ease;
                }

                &.swiper-pagination-bullet-active::after{
                    opacity: 1;
                    background-color: #FAFAFA;
                }
            }
        }
    }

    &__img {
        width: 100%;
        height: 100%;
  
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            will-change: transform;
            transition: transform 0.35s ease;
        }
    }

    &:not(.card-product--long) &__img{
        &:hover{
            img{
                transform: scale(103%);
            }
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        padding: 24px 16px 16px;  
        flex-grow: 1;
    }

    &__main {
        display: flex;
        flex-direction: column;
        flex-grow: 1;

        p{
            display: -webkit-box;
            -webkit-box-orient: vertical;
            overflow: hidden;
            -webkit-line-clamp: 3;
            font-weight: 400;
            font-size: 14px;
            line-height: 130%;
        }
    }

    &__title {
        cursor: pointer;
        display: flex;
        align-items: center;
        color: inherit;
        text-decoration: none;
        margin-bottom: 12px;
        transition: color 0.35s ease;

        &:hover{
            color: $blue;
        }


        h4{
            display: -webkit-box;
            -webkit-box-orient: vertical;
            overflow: hidden;
            -webkit-line-clamp: 3;
            font-weight: 700;
            font-size: 24px;
            line-height: 120%;
            letter-spacing: 0.15px;
        }

        span{
            display: inline-flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 16px;
            right: 16px;
            z-index: 1;
            height: 30px;
            text-align: center;
            font-weight: 600;
            font-size: 14px;
            line-height: 100%;
            color: #F5F5F5;
            padding: 0 16px;
            border-radius: 20px;
            background-color: $blue;
        }
    }

    &__price {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding-top: 16px;
        margin: auto -6px 4px;

        &>*{
            margin: 0 6px 12px;
            transition: color 0.5s ease;
        }

        strong{
            font-weight: 700;
            font-size: 28px;
            line-height: 120%;
            color: $blue;
        }

        span{
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 120%;
            color: #BFBFBF;
            text-decoration: line-through;
        }
    }

    &__actions {
        margin-top: auto;
    }


    &__button {
        width: 100%;
        padding: 0 24px;
        
        &:hover{
            background-color: #fff;
            // border-color: #fff;
            color: $blue;
            fill: $blue;
        }
    }

    &__settings {
        box-sizing: border-box;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 54px;
        border-radius: 50px;
        border: 2px solid $blue;
        padding: 0 18px;
        display: none;
    }

    &__settings-button{
        margin-left: 16px;
    }

    &__settings-title {
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        letter-spacing: 0.150002px;
        color: $blue;
        margin-right: 16px;
    }

    &__input {
        .input-number{

            &__button{
                width: 20px;
                height: 20px;

                .icon{
                    width: 12px;
                    height: 12px;
                }
            }

            &__field{
                color: $blue;
            }
        }
    }

    &__delete{
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        margin-left: 24px;
        fill: #F5222D;
        transition: fill 0.35s ease;

        @media only screen and (max-width: $bp-md) {
            margin-left: auto;
        }

        &:hover{
            fill: #A8071A;
        }

        .icon{
            width: 100%;
            height: 100%;
        }
    }

    &:not(.is-added) &__button{
        animation: fadeIn 0.5s ease forwards;
    }

    &.is-added &__button{
        display: none;
    }

    &.is-added &__settings{
        display: flex;
        animation: fadeIn 0.5s ease forwards;
    }

    &--long {
        flex-direction: row;
        border: 1.5px solid #F5F5F5;
        padding-left: 214px;
        min-height: 120px;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            padding-left: 0;
        }
    }

    &--long &__settings{
        display: flex;
        border: 0;
        padding: 0;
        height: auto;
    }

    &--long &__img{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 214px;

        @media only screen and (max-width: $bp-md) {
            position: static;
            width: 100%;
            height: 250px;
        }
    }

    &--long &__content{
        flex-direction: row;
        align-items: center;
        padding: 16px 24px;
        
        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            align-items: initial;
        }
        
        @media only screen and (max-width: $bp-md) {
            padding: 24px 16px;
        }
    }

    &--long &__actions{
        margin-top: 0;
        margin-left: 16px;

        @media only screen and (max-width: $bp-lg) {
            margin-left: 0;
            margin-top: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-top: 24px;
        }
    }

    &--long &__input{
        order: -1;
    }

    &--long &__settings{
        @media only screen and (max-width: $bp-lg) {
            justify-content: flex-start;
        }
    }

    &--long &__title {
        margin-bottom: 8px;

        h4{
            font-size: 20px;
            line-height: 24px;
        }

        span{
            position: static;
            padding: 0 12px;
            height: 26px;
            margin-left: 10px;
            flex-shrink: 0;
        }
    }

    &--long &__price{
        padding: 0;
        margin: 0 -4px -10px;

        &>*{
            margin: 0 4px 10px;
        }

        strong{
            font-weight: 800;
            font-size: 20px;
            line-height: 23px;
        }

        span{
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
        }
    }

    &--long &__input{
        .input-number{
            &__button{
                width: 24px;
                height: 24px;

                .icon{
                    width: 16px;
                    height: 16px;
                }
            }

            &__field{
                color: #262626;
            }
        }
    }
}