@import "@styles/vars.scss";

.select-links {
    position: relative;
    display: flex;
    font-family: 'Raleway';
    font-size: 14px;
    font-weight: 500;
    line-height: 100%;
    letter-spacing: 0.2px;

    &__trigger {
        box-sizing: border-box;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        color: $blue;
        fill: $blue;
        transition: color 0.35s ease, fill 0.35s ease;

        &:hover{
            color: $blue-dark;
            fill: $blue-dark;
        }

        span{

        }

        .icon{
            width: 24px;
            height: 24px;
            flex-shrink: 0;
            margin-left: 12px;
            transition: transform 0.35s ease;
        }
    }

    &__dropdown {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 100%;
        right: 0;
        z-index: 10;
        min-width: 160px;
        max-width: 200px;
        background-color: #fff;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.12);
        border-radius: 8px;
        padding: 8px 0;
        overflow: hidden;
        opacity: 0;
        visibility: hidden;
        transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;
        
    }

    &__item {
        color: #000000;
        text-decoration: none;
        line-height: 120%;
        padding: 4px 16px;
        margin-bottom: 8px;
        transition: background-color 0.35s ease;

        &:last-child{
            margin-bottom: 0;
        }

        &:hover{
            background-color: #F1FAFF;
        }
    }
    
    &.is-open &__dropdown{
        opacity: 1; 
        visibility: visible;
        transform: translateY(12px);
    }

    &.is-open &__trigger .icon{
        transform: rotate(-180deg);
    }
}
