@import "@styles/vars.scss";
@import "@styles/animations.scss";

.header {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    height: 74px;
    position: sticky; 
    top: 0;
    z-index: 50;
    background-color: #fff;

    // @media only screen and (max-width: $bp-xl) {
    //     padding: 20px 0;
    // }

    @media only screen and (max-width: $bp-lg) {
        height: 58px;
    }

    &.is-active .logo{
        width: 195px;
    }

    &__container {
        box-sizing: border-box;
        max-width: 1290px;
        padding: 0 16px;

        @media only screen and (max-width: $bp-xxl) {
            padding: 0 32px;
        }

        @media only screen and (max-width: $bp-xl) {
            padding: 0 20px;
        }
    }

    &__inner{
        display: flex;
        align-items: center;
    }

    &__logo {
        display: flex;
        height: 42px;
        margin-right: 48px;

        @media only screen and (max-width: $bp-xxl) {
            margin-right: 40px;
        }

        @media only screen and (max-width: $bp-xl) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 40px;
            margin-right: 16px;
            overflow: hidden;
            width: 75px;
            transition: width 0.4s ease;
        }

        @media only screen and (max-width: $bp-md) {
            height: 38px;
            width: 72px;
            margin-right: 8px;
        }

        img{
            flex-shrink: 0;
            height: 100%;
            width: auto;
        }

        .logo{
            &__label {
                margin-left: 8px;

                @media only screen and (max-width: $bp-lg) {
                    margin-left: 16px;
                }
            }
        }
    }

    &__button{
        width: 125px;
        height: 45px;
        margin-right: 30px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__link-title {
    }

    &__burger {
        cursor: pointer;
        position: relative;
        display: none;
        align-items: center;
        justify-content: center;
        width: 21px;
        height: 12px;
        padding: 0;
        margin-left: 24px;
        background: none;
        border: none;
        order: 2;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }

        span, 
        &::before,
        &::after{
            display: flex;
            width: 21px;
            height: 2px;
            border-radius: 2px;
            background-color: #CECECE;
            position: absolute;
            top: 50%;
            left: 50%;
            transition: transform 0.35s ease, opacity 0.35s ease, background-color 0.35s ease;

        }

        span{
            transform: translate(-50%, -50%);
        }

        &::after{
            content: "";
            transform: translate(-50%, 4px);
        }

        &::before{
            content: "";
            transform: translate(-50%, -6px);
        }

        &.is-active{
            span{
                background-color: #000;
                opacity: 0;
            }

            &::after{
                background-color: #000;
                transform: translate(-50%, -1px) rotate(45deg);
            }
    
            &::before{
                background-color: #000;
                transform: translate(-50%, -1px) rotate(-45deg);
            }
        }

        &:hover{
            span,
            &::before,
            &::after{
                background-color: $blue;
            }
        }
    }

    &__menu {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        flex-grow: 1;
        transition: visible 0.5s ease, opacity 0.5s ease, transform 0.5s ease;

        @media only screen and (max-width: $bp-lg) {
            overflow: hidden;
            flex-direction: column;
            width: 100%;
            position: absolute;
            top: 100%;
            right: 0;
            background-color: #fff;
            padding: 10px 0 45px;
            opacity: 0;
            pointer-events: none;
            transform: translateY(-25%);
            z-index: -2;
            &>*{
                opacity: 0;
                transition: opacity 0.24s ease;
            }
        }

        &.is-active{
            pointer-events: all;
            opacity: 1;
            transform: translateY(0);
            &>*{
                opacity: 1;
                transition: opacity 0.25s ease 0.15s;
            }
        }
    }

    &__menu-content {
        position: relative;
        box-sizing: border-box;
        display: flex;
        flex-grow: 1;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            justify-content: flex-end;
            margin-left: auto;
            background-repeat: no-repeat;
            background-size: 220px auto;
            background-image: url(../../assets/media/header/menu-pic.svg);
            padding: 35px 50px 60px;
            border-bottom: 2px solid #dff3fe;
        }

        @media only screen and (max-width: $bp-md) {
            background-size: 164px auto;
            
        }
    }

    &__nav {
        position: relative;
        display: flex;
        align-items: center;
        width: 450px;

        @media only screen and (max-width: $bp-xl) {
            width: 420px;
        }

        @media only screen and (max-width: 1119px) {
            width: 320px;
        }

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            align-items: flex-end;
            width: 100%;
        }

        .nav{
            
            &__list{
                display: flex;
                align-items: center;
                flex-wrap: nowrap;
                width: max-content;

                @media only screen and (max-width: $bp-lg) {
                    flex-direction: column;
                    align-items: flex-end;
                }
            }

            &__item{
                cursor: pointer;
                box-sizing: border-box;
                display: inline-flex;
                font-family: 'Raleway';
                margin-right: 32px;
                white-space: nowrap;
                text-decoration: none;
                font-size: 14px;
                line-height: 120%;
                font-weight: 700;
                flex-shrink: 0;
                color: $blue;
                transition: color 0.35s ease, fill 0.35s ease, opacity 0.35s ease, background-color 0.35s ease;

                &:hover,
                &.is-active{
                    color: #000;
                    fill: #000;
                }

                @media only screen and (max-width: $bp-xl) {
                    font-size: 12px;
                    margin-right: 24px;
                }

                @media only screen and (max-width: $bp-lg) {
                    margin-bottom: 25px;
                    margin-right: 0;
                    text-align: right;
                }

                &:last-of-type{
                    // margin-right: 0;
                    margin-bottom: 0;
                }

                &--hidden{
                    display: none;

                    @media only screen and (max-width: $bp-lg) {
                        display: inline-flex;
                    }
                }
            }

            &__more{
                display: flex;
                align-items: center;
                position: relative;

                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }

            }

            &__trigger{
                cursor: pointer;
                display: none;
                align-items: center;
                justify-content: center;
                width: 24px;
                height: 24px;
                flex-shrink: 0;
                fill: #D9D9D9;
                transition: fill 0.35s ease;
                margin-right: 32px;

                @media only screen and (max-width: $bp-xl) {
                    margin-right: 24px;
                }
        
                &:hover{
                    fill: $blue;
                }
        
                .icon{
                    width: 100%;
                    height: 100%;
                }


                &.is-visible{
                    display: flex;

                    @media only screen and (max-width: $bp-lg) {
                        display: none;
                    }
                }
            }

            &__dropdown{
                box-sizing: border-box;
                display: block;
                min-width: 130px;
                max-width: 300px;
                padding: 8px 0;
                background-color: #fff;
                box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.12);
                border-radius: 8px;
                position: absolute;
                top: 100%;
                right: 0;
                opacity: 0;
                visibility: hidden;
                transform: translateY(15px);
                transition: opacity 0.35s ease, visibility 0.35s ease, transform 0.5s ease;

                &.is-active{
                    visibility: visible;
                    opacity: 1;
                    transform: translateY(30px);
                }
            }

            &__dropdown-inner{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                max-height: 125px;
                padding: 0 16px;
                overflow: hidden auto;
            }

            &__dropdown-inner .nav__item{
                position: relative;
                // width: 100%;
                padding-top: 4px;
                padding-bottom: 4px;
                // padding: 4px 16px;
                margin: 0 0 8px;
                font-weight: 500;
                color: #000;

                &::before{
                    content: "";
                    pointer-events: none;
                    box-sizing: border-box;
                    display: block;
                    position: absolute;
                    width: 400%;
                    height: 100%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: -1;
                    background-color: #F1FAFF;
                    opacity: 0;
                    transition: opacity 0.35s ease;
                }

                &:hover,
                &.is-active{

                    &::before{
                        opacity: 1;
                    }
                }

                &:last-of-type{
                    margin-bottom: 0;
                }
            }
        }   

    }

    &__search{
        @media only screen and (max-width: $bp-lg) {
            margin-left: auto;
            position: relative;
            flex-grow: 1;
            justify-content: flex-end;
            order: 1;
        }

    }

    &__contacts {
        order: 2;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-lg) {
            padding: 0 24px;
            margin-top: 50px;
            width: 100%;
            box-sizing: border-box;

        }
    }
    
    &__contacts-row {
        display: flex;
        align-items: center;
    }

    &__contacts-label {
        display: none;
        margin-right: 24px;

        @media only screen and (max-width: $bp-lg) {
            display: inline-flex;
            margin-bottom: 0;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__contacts-link {
        display: inline-flex;
        text-decoration: none;
        font-family: 'Inter';
        color: #000;
        transition: color 0.35s ease;

        &:hover{
            color: $blue;
        }

        @media only screen and (max-width: $bp-xl) {
            font-size: 12px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            color: $blue;

            &:hover{
                color: $blue-dark;
            }
        }

    }


    &__socials {
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        margin: 0 24px 0 auto;

        @media only screen and (max-width: $bp-lg) {
            align-items: flex-end;
            width: 100%;
            padding: 0 26px;
            margin: 30px 0 0;
            order: 2;
        }

        .socials {

            @media only screen and (max-width: $bp-lg) {
               
            }

            &__partner {
                display: none;

                @media only screen and (max-width: $bp-lg) {
                    display: flex;
                    flex-direction: column;
                    margin-right: 20px;
                }
            }
        
            &__partner-label {

                @media only screen and (max-width: $bp-lg) {
                    font-size: 12px;
                    margin-bottom: 12px;
                }
            }
        
            &__partner-logo {
                width: 128px;
                height: 23px;
            }
        
            &__media {

                @media only screen and (max-width: $bp-lg) {
                    margin-top: 15px;
                }
            }
        
            &__item {
                width: 24px;
                height: 24px;
                margin-right: 5px;
                fill: #D6D6D6;
                transition: fill 0.35s ease;

                @media only screen and (max-width: $bp-lg) {
                    width: 32px;
                    height: 32px;
                }

                &:hover{
                    fill: $blue;
                    opacity: 1;
                }
                
                &:last-of-type{
                    margin-right: 0;
                }
            }
        
            &__item-icon {
                width: 100%;
                height: 100%;
            }
        }
    }

    &__login {
        margin-left: auto;
        margin-right: 48px;
        font-size: 14px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 50px;
        }

        .icon{
            width: 20px;
            height: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }
}