@import '@styles/vars.scss';

.aside-block {
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    max-width: 1315px;
    padding: 0 55px 0 85px;
    position: absolute;
    top: 0;
    left: 50%;
    height: 100%;
    z-index: 20;
    pointer-events: none;
    transform: translateX(-50%);

    &::after{
        content: "";
        pointer-events: none;
        display: block;
        width: 330px;
        height: 700px;
        background: linear-gradient(180deg, rgba(241, 250, 255, 0) 0%, #F1FAFF 39.58%);
        position: absolute;
        bottom: 100px;
        right: 0;
        z-index: 2;
    }
    
    @media only screen and (max-width: $bp-xl){
        display: none;
    }

    &__inner {
        pointer-events: all;
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        margin-left: auto;
        width: 270px;
        flex-shrink: 0;
        background-color: $blue;
        padding: 40px 28px 26px;
        border-radius: 20px;
        color: #fff;
        position: sticky;
        top: 80px;
        min-height: 354px;
        margin-bottom: 110px;
    }

    &__title{
        display: flex;
        text-align: left;
        font-size: 24px;
        margin-bottom: 35px;
    }

    &__radio-tumbler {
        margin-bottom: 25px;
        height: 50px;
    }

    &__content {
        width: 100%;
        margin-bottom: auto;
    }

    &__item {
        display: none;

        &.is-open{
            display: block;
            animation: fadeInTop 0.5s ease forwards;
        }
    }

    &__price-block {
        width: 100%;
    }

    &__price-old {
        display: inline-flex;
        font-family: 'Inter';
        color: $blue-dark;
        position: relative;

        &::after{
            content:"";
            width: 100%;
            background-color: #F095BA;
            height: 4px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%) rotate(-12deg);
        }
    }

    &__price {
        font-family: 'Inter';
        font-size: 40px;   
        margin-bottom: 5px;
        margin-left: 5px;
    }

    &__text {
        color: #fff;
        opacity: 0.5;
        margin-left: 5px;
    }

    &__button {
        margin-top: 55px;
    }
}
