@import "@styles/vars.scss";

.product {
    font-family: 'Raleway';
    display: flex;
    align-items: center;

    @media only screen and (max-width: $bp-lg) {
        flex-direction: column;
        align-items: initial;
    }

    &__slider{
        width: 550px;
        height: 450px;
        flex-shrink: 0;
        margin-bottom: auto;
        border-radius: 16px;
        overflow: hidden;

        @media only screen and (max-width: $bp-xl) {
            width: 50%;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            height: 350px;
            margin-bottom: 40px;
        }

        .slider {

            &__inner {
                height: 100%;
            }

            &__wrapper {
                height: 100%;
            }

            &__slide {
                height: 100%;
            }

            &__navigation {
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                z-index: 2;
                width: 100%;
                margin: 0;
                justify-content: space-between;
                padding: 0 16px;
            }

            &__button {
                width: 40px;
                height: 40px;

                .icon{
                    width: 24px;
                    height: 24px;
                }
            }
        }
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__main {
        width: calc(100% - 590px);
        margin-left: 40px;

        @media only screen and (max-width: $bp-xl) {
            width: calc(50% - 32px);
            margin-left: 32px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-left: 0;
        }
    }

    &__title {
        display: flex;
        align-items: center;
        margin-bottom: 16px;
        font-weight: 800;
        font-size: 32px;
        line-height: 120%;
        letter-spacing: 0.15px;
        color: #262626;

        @media only screen and (max-width: $bp-lg) {
            font-size: 28px;
        }

        span{
            display: inline-flex;
            align-items: center;
            justify-content: center;
            font-weight: 600;
            font-size: 14px;
            line-height: 100%;
            text-align: center;
            color: #F5F5F5;
            padding: 0 16px;
            height: 30px;
            flex-shrink: 0;
            background-color: $blue;
            border-radius: 16px;
            margin-left: 10px;
        }
    }

    &__price {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: 0 -4px 16px;

        &>*{
            margin: 0 4px 16px;
        }

        strong{
            font-weight: 800;
            font-size: 24px;
            line-height: 28px;
            letter-spacing: -0.11px;
            color: $blue;

            @media only screen and (max-width: $bp-lg) {
                font-size: 20px;
                line-height: 22px;
            }
        }

        span{
            font-weight: 400;
            font-size: 18px;
            line-height: 21px;
            letter-spacing: -0.11px;
            text-decoration: line-through;
            color: #BFBFBF;

            @media only screen and (max-width: $bp-lg) {
                font-size: 16px;
                line-height: 19px;
            }
        }
    }

    &__actions {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: 0 -5px 16px;

        &>*{
            margin: 0 5px 16px;

            @media only screen and (max-width: $bp-xl) {
                flex-grow: 1;
            }

            @media only screen and (max-width: $bp-lg) {
                width: 100%;
            }
        }
    }

    &__info {

        p{
            font-size: 16px;
            line-height: 120%;
            letter-spacing: 0.15px;
            color: #858585;
            margin-bottom: 16px;

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
                line-height: 17px;
            }

            &:last-child{
                margin-bottom: 0;
            }
        }
    }

    &__info-title {
        color: #262626;
        margin-bottom: 16px;
    }

    &__settings {
        box-sizing: border-box;
        align-items: center;
        justify-content: center;
        height: 54px;
        border-radius: 50px;
        border: 3px solid $blue;
        padding: 0 24px;
        display: none;
    }

    &__settings-button{
        margin-left: 16px;
    }

    &__settings-title {
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        letter-spacing: 0.150002px;
        color: $blue;
        margin-right: 16px;
    }
 
    &:not(.is-added) &__button{
        animation: fadeIn 0.5s ease forwards;
    }

    &.is-added &__settings{
        display: inline-flex;
        animation: fadeIn 0.5s ease forwards;
    }

    &.is-added &__button{
        display: none;
    }
}