@import "@styles/vars.scss";

.search-input {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    width: 100%;
    height: 48px;
    font-size: 14px;
    line-height: 24px;

    &__field {
        outline: 0;
        box-sizing: border-box;
        display: flex;
        width: 100%;
        height: 100%;
        padding: 0 46px;
        border-radius: 50px;
        border: 2px solid $blue;
        color: #2B3240;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        transition: color 0.35s ease, border-color 0.35s ease;
    }

    &__button,
    &__trigger {
        cursor: pointer;
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        transition: fill 0.35s ease;

        .icon{
            width: 100%;
            height: 100%;
        }
    }

    &__button{
        left: 12px;
        fill: $blue;

        &:hover{
            fill: $blue-dark;
        }
    }

    &__trigger {
        right: 12px;
        fill: #D9D9D9;
        
        &:hover{
            fill: $blue;
        }
    }

    &--square &__field{
        border-radius: 16px;
    }
}

.search-dropdown {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 100%;
    position: fixed;
    top: 74px;
    left: 0;
    z-index: 50;
    // opacity: 0;
    // visibility: hidden;
    background-color: #fff;
    border-top: 1px solid #F0F0F0;
    padding: 24px 0;
    transition: opacity 0.35s ease, visibility 0.35s ease;
    max-height: calc(100% - 74px);
    box-shadow: 0px 16px 10px rgb(23 41 51 / 5%);

    @media only screen and (max-width: $bp-lg) {
        top: 58px;
        max-height: calc(100% - 58px);
    }

    &__container{
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        max-width: 1290px;
        margin: 0 auto;
        max-height: 100%;
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            padding: 0 20px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 0 16px;
        }
    }

    &__header{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 15px;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            padding-bottom: 16px;
            margin-bottom: 16px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__title {
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 32px;
        line-height: 130%;
        width: 100%;
        margin-bottom: 4px;
        color: #262626;

        @media only screen and (max-width: $bp-lg) {
            font-size: 28px;
        }
    }

    &__subtitle {
        font-family: 'Raleway';
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: #8C8C8C;
        margin-right: 16px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__reset {
        font-family: 'Raleway';
        font-weight: 500;
        font-size: 16px;
        line-height: 120%;
        text-decoration: none;
        color: #F095BA;
        transition: color 0.35s ease;

        &:hover{
            color: #ff2d4c;
        }
    }

    &__list{
        overflow: hidden auto;
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            margin: 0 -20px;
            width: calc(100% + 40px);
        }

        @media only screen and (max-width: $bp-md) {
            margin: 0 -16px;
            width: calc(100% + 32px);
        }

        .search-block{
            padding: 24px 0;
            border-top: 1px solid #F0F0F0;
            
            @media only screen and (max-width: $bp-lg) {
                padding: 16px 0;
            }

            &:first-child{
                padding-top: 0;
                border-top: 0;
            }

            &:last-child{
                padding-bottom: 0;
            }
        }
    }

    &__button{
        height: 42px;
        padding: 0 24px;
        font-size: 14px;
        flex-shrink: 0;
        margin-top: 16px;
    }
}

.search-block {

    &__header{
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }
    }

    &__title {
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 24px;
        line-height: 130%;
        margin-bottom: 4px;
    }

    &__subtitle {
        font-family: 'Raleway';
        font-weight: 600;
        font-size: 18px;
        line-height: 130%;
        color: #8C8C8C;
        margin-top: 4px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
        }
    }

    &__list {
        display: flex;
        flex-direction: column;

        &>*{
            margin-bottom: 16px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 0;
            }

            &:last-child{
                margin-bottom: 0;
            }
        }

        .card-course{
            @media only screen and (max-width: $bp-lg) {
                flex-direction: row;
                align-items: center;
                padding: 12px 16px;
                background-color: transparent;
                min-height: auto;
                border-radius: 0;
        
                &:hover{
                    background-color: #fff;
                }
            }
        
            &__img{
        
                @media only screen and (max-width: $bp-lg) {
                    position: relative;
                    width: 50px;
                    height: 50px;
                    flex-shrink: 0;
                    border-radius: 4px;
                    margin-right: 15px;
                    border: 1px solid rgba(58, 179, 254, 0.25);
                    order: -1;
                    
                    &::before,
                    &::after {
                        content: none;
                    }
                }
        
                img{
                    @media only screen and (max-width: $bp-md) {
                        object-position: center;
                        object-fit: cover;
                    }
                }
            }
        
            &__header{
                @media only screen and (max-width: $bp-lg) {
                    margin-bottom: 0;
                }
            }
        
            &__title{
                @media only screen and (max-width: $bp-lg) {
                    color: #262626 !important;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 120%;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    -webkit-line-clamp: 2;
                }
            }
        
            &__level{
                @media only screen and (max-width: $bp-lg) {
                    font-family: 'Raleway';
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 120%;
                    color: #BFBFBF !important;
                }
            }
        
            &__tooltip{
                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }
            }
        
            &__body{
                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }
            }
        }
    }

    &__button{
        margin-top: 16px;

        @media only screen and (max-width: $bp-lg) {
            height: 42px;
            font-size: 14px;
            padding: 0 24px;
        }
    }
}

.search {
    display: flex;
    align-items: center;

    &__input {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.35s ease, visibility 0.35s ease;
    }

    &__trigger {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        fill: #BFBFBF;
        transition: fill 0.35s ease, opacity 0.35s ease, visibility 0.35s ease;

        &:hover{
            fill: $blue;
        }

        .icon{
            width: 100%;
            height: 100%;
        }
    }

    &__dropdown {
        // display: none;
    }

    &.is-open &__input{
        opacity: 1;
        visibility: visible;
    }

    &.is-open &__trigger{
        opacity: 0;
        visibility: hidden;
    }

    .search-block{
        
        &__header{
            margin-bottom: 0;
            padding-bottom: 16px;
            background-color: #fff;
            position: sticky;
            top: 0;
            z-index: 1;
                    
            @media only screen and (max-width: $bp-lg) {
                padding-right: 20px;
                padding-left: 20px;
            }

            @media only screen and (max-width: $bp-md) {
                padding-right: 16px;
                padding-left: 16px;
            }
        }

        &__list{
            &>*{
                margin-bottom: 0;
            }
        }

        .card-teacher{
            margin-bottom: 0;
            
            &:hover{
                background-color: #F1FAFF;
            }
        }
    }

    &__input ~ &__dropdown{
        visibility: hidden;
        opacity: 0;
    }

    &__input.is-focus + &__dropdown{
        opacity: 1;
        visibility: visible;
    }
}
