@import '@styles/vars.scss';

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/modal";
@import "~bootstrap/scss/transitions";

.fade{
	transition: opacity 0.25s linear;
}
.modal {
	&.fade &-dialog{
		transform: translateY(15px);
		transition: transform 0.25s ease-out;

	}

	&.show &-dialog{
		transform: none;
	}

	&-backdrop{
		&.show{
			background: #000;
			opacity: 0.45;
		}
	}

	&-dialog {
		border-radius: 0;
		border: none;
		max-width: 488px;
		margin-right: auto;
		margin-left: auto;
	}

	&-content {
		box-sizing: border-box;
		border: none;
		background-color: #fff;
		border-radius: 10px;
		min-height: 595px;
		padding: 50px 65px 0;
		border-radius: 44px;
		display: flex;

		@media only screen and (max-width: $bp-lg){
			padding: 50px 45px 0;
			min-height: 488px;
		}

		@media only screen and (max-width: $bp-md){
			padding-right: 32px;
			padding-left: 32px;
		}
	}

	&-close {
		position: absolute;
		right: 32px;
		top: 32px;
		width: 32px;
		height: 32px;
		background-color: transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		cursor: pointer;

		@media only screen and (max-width: $bp-lg){
			top: 22px;
			right: 22px;
		}

		@media only screen and (max-width: $bp-md) {
			width: 24px;
			height: 24px;
		}

		&:hover {
			.icon{
				fill: $blue;
			}
		}

		.icon {
			width: 100%;
			height: 100%;
			transition: fill 0.3s ease;
			fill: $blue-dark;

			@media only screen and (max-width: $bp-md) {
				width: 24px;
				height: 24px;
			}
		}

	}

	&__title{
		font-family: 'Inter';
		font-weight: 700;
		font-size: 40px;
		line-height: 120%;
		margin-bottom: 48px;

		@media only screen and (max-width: $bp-lg){
			font-size: 36px;
		}

		@media only screen and (max-width: $bp-md){
			font-size: 32px;
		}
	}

	&__main{
		display: flex;
		flex-direction: column;
		padding-bottom: 80px;
		flex-grow: 1;

		@media only screen and (max-width: $bp-lg){
			padding-bottom: 54px;
		}

		@media only screen and (max-width: $bp-md){
			padding-bottom: 50px;
		}
	}

	&__form{
		display: flex;
		flex-direction: column;
		flex-grow: 1;

		form{
			display: flex;
			flex-direction: column;
			flex-grow: 1;
		}
	}

	&__input{
		width: 100%;
		margin-bottom: 25px;
	}

	&__button{
		margin-top: auto;
		font-size: 20px;
		height: 60px;

		@media only screen and (max-width: $bp-lg){
			font-size: 16px;
		}
	}

	&--course &-dialog{
		max-width: 1024px;
	}

	&--course &-content{
		flex-direction: row;
	}

	&--course &__img{
		object-fit: contain;
		margin-left: auto;
		width: calc(100% - 425px);
		max-width: 408px;
		margin-top: auto;

		@media only screen and (max-width: $bp-md){
			display: none;
		}
	}
	&--course &__form form{
		align-items: flex-start;

		@media only screen and (max-width: $bp-md){
			align-items: initial;
		}
	}
	&--course &__main{
		max-width: 375px;

		@media only screen and (max-width: $bp-md){
			max-width: none;
		}
	}

	&--ok &-dialog{
	}
	
	&--ok &-content{
		min-height: 468px;
		padding-top: 70px;
		
		@media only screen and (max-width: $bp-lg){
			min-height: 390px;
		}
	}

	&--ok &__img{
		width: 115px;
		height: 115px;
		order: -1;
		margin: 0 auto 35px;

		@media only screen and (max-width: $bp-md){
			width: 90px;
			height: 90px;
			margin-bottom: 25px;
		}
	}
	
	&--ok &__main{
		align-items: center;
		text-align: center;
	}
	
	&--ok &__title{
		margin-bottom: 18px;
	}

    &--cart &-close{
        top: 32px;
        right: 24px;
        width: 24px;
        height: 24px;

        @media only screen and (max-width: $bp-md) {
            top: 26px;
        }
    }

    &--cart &-dialog{
        max-width: 892px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-height: calc(100% - var(--bs-modal-margin) * 2);
        overflow: hidden;

        @media only screen and (max-width: $bp-md) {
            justify-content: flex-start;
            height: 100%;
            max-height: 100%;
            margin: 0;
            transform: none !important;
        }
    }
	
    &--cart &-content{
        padding: 24px;
        border-radius: 16px;
        min-height: auto;
        max-height: 100%;
        overflow: hidden;

        @media only screen and (max-width: $bp-md) {
            border-radius: 0;
        }
    }

    &--cart &__main{
        max-height: 100%;
        overflow: hidden;
        padding: 0;
    }

    &__cart{
        max-height: 100%;
        overflow: hidden;
    }
}   


.modal-cart {
    font-family: 'Raleway';
    display: flex;
    flex-direction: column;

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 24px;
        }
    }

    &__title {
        display: inline-flex;
        align-items: center;
        font-weight: 700;
        font-size: 32px;
        line-height: 38px;
        color: #262626;
        padding-right: 32px;

        @media only screen and (max-width: $bp-md) {
            font-size: 24px;
            line-height: 28px;
        }

        span{
            font-weight: 600;
            font-size: 18px;
            line-height: 21px;
            color: #bfbfbf;
            margin-left: 24px;
            flex-shrink: 0;

            @media only screen and (max-width: $bp-lg) {
                margin-left: 16px;
            }

            @media only screen and (max-width: $bp-md) {
                margin-left: 12px;
            }
        }
    }

    &__body {
        display: flex;
        flex-direction: column;
        overflow-y: auto;

        .card-product{
            flex-shrink: 0;
        }

        &>*{
            margin-bottom: 16px;

            @media only screen and (max-width: $bp-md) {
                margin-bottom: 24px;
            }

            &:last-child{
                margin-bottom: 32px;

                @media only screen and (max-width: $bp-md) {
                    margin-bottom: 24px;
                }
            }
        }
    }

    &__footer {
        display: flex;
        align-items: center;    
        flex-wrap: wrap;
        padding-top: 32px;
        border-top: 1.5px solid #F5F5F5;

        @media only screen and (max-width: $bp-md) {
            padding-top: 24px;
        }
    }

    &__price {
        display: flex;
        align-items: center;
        margin: 0 12px -12px 0;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            justify-content: center;
            margin-bottom: 12px;
        }

        strong{
            font-weight: 700;
            font-size: 28px;
            line-height: 33px;
            letter-spacing: -0.11px;
            color: $blue;
        }

        span{
            font-weight: 500;
            font-size: 20px;
            line-height: 23px;
            letter-spacing: -0.11px;
            text-decoration: line-through;
            color: #BFBFBF;
        }

        &>*{
            margin: 0 8px 12px 0;
        }
    }

    &__reset {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 600;
        font-size: 18px;
        line-height: 100%;
        padding: 0 24px;
        height: 46px;
        margin-left: auto;
        letter-spacing: -0.11px;
        color: #BFBFBF;
        fill: #BFBFBF;
        transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease;

        @media only screen and (max-width: $bp-md) {
            margin-left: 0;
            width: calc(50% - 8px);
        }

        @media only screen and (max-width: $bp-sm) {
            width: 46px;
            height: 46px;
            padding: 0;
            border: 1px solid #D9D9D9;
            border-radius: 4px;

            span{
                display: none;
            }
        }

        &:hover{
            color: $blue;
            fill: $blue;
            border-color: $blue;
        }

        .icon{
            width: 24px;
            height: 24px;
            margin-left: 16px;

            @media only screen and (max-width: $bp-sm) {
                margin-left: 0;
            }
        }
    }

    &__button {
        height: 46px;
        margin-left: 16px;

        @media only screen and (max-width: $bp-md) {
            width: calc(50% - 8px);
        }

        @media only screen and (max-width: $bp-sm) {
            width: auto;
            flex-grow: 1;
        }
    }
}
