@import "@styles/vars.scss";

.section-path{
    position: relative;
    color: #000;
    padding: 135px 0 65px;
    
    @media only screen and (max-width: $bp-xl){
        padding-top: 100px;
    }

    @media only screen and (max-width: $bp-lg){
        padding-bottom: 40px;
    }
    
    &__container{
        padding: 0 37.5px;
        position: relative;

        @media only screen and (max-width: $bp-md){
            padding: 0 16px;
        }
    }

    &__inner{
        z-index: 3;
    }

    &__title{
        font-family: 'Raleway';
        text-align: center;
        font-weight: 700;
        font-size: 60px;
        line-height: 110%;

        @media only screen and (max-width: $bp-xl){
            font-size: 45px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 47px;
            text-align: left;
            margin-left: 45px;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 35px;
            margin-left: 20px;
        }
    }

    &__content{
        position: relative;
        display: block;
        // height: 2872px;
        height: 2674px;
        margin-top: -28px;

        @media only screen and (max-width: $bp-xl){
            height: 1962px;
            // height: 2108px;
        }

        @media only screen and (max-width: $bp-lg){
            height: 3659px;
            margin-top: 105px;
        }

        @media only screen and (max-width: $bp-md){
            height: 2702px;
            margin-top: 75px;
        }

        @media only screen and (max-width: $bp-sm) {
            margin-top: 60px;
        }
    }

    &__stages {
    }

    &__main._run &__stage {
        opacity: 1;
        visibility: visible;
        margin-top: 0;
    }

    &__stage {
        position: absolute;
        display: flex;
        color: #000;
        max-width: 368px;
        z-index: 3;
        transition: opacity 0.75s ease, visibility 0.75s ease, margin 0.75s ease;

        @media only screen and (min-width: $bp-lg + 1){
            opacity: 0;
            visibility: hidden;
            margin-top: 30px;
        }


        // @media only screen and (max-width: $bp-lg) {
        //     display: none;
        // }


        @media only screen and (max-width: $bp-lg){
            flex-direction: column;
        }

        &--1{
            top: 124px;
            left: 55px;
            transition-delay: 0.75s;

            @media only screen and (max-width: $bp-xl){
                max-width: 278px;
                top: 90px;
                left: 75px;
            }

            @media only screen and (max-width: $bp-lg){
                top: -32px;
                right: 180px;
                left: auto;
            }

            @media only screen and (max-width: $bp-md){
                top: -40px;
                right: 50px;
            }

            @media only screen and (max-width: $bp-sm){
                width: 208px;
                top: -20px;
            }
        }

        &--2{
            top: 370px;
            left: 50%;
            transform: translateX(-115px);
            max-width: 315px;
            transition-delay: 1s;

            @media only screen and (max-width: $bp-xl){
                max-width: 230px;
                top: 280px;
            }

            @media only screen and (max-width: $bp-lg){
                top: 280px;
                right: 280px;
                left: auto;
                transform: none;
            }

            @media only screen and (max-width: $bp-md){
                top: 192px;
                right: auto;
                left: 20px;
                width: 190px;
            }

            @media only screen and (max-width: $bp-sm) {
                top: 218px;
            }
        }

        &--3{
            max-width: 300px;
            top: 90px;
            right: 240px;
            transition-delay: 1.25s;
            
            @media only screen and (max-width: $bp-xl){
                right: 180px;
                max-width: 235px;
            }

            @media only screen and (max-width: $bp-lg){
                width: 280px;
                right: 200px;
                top: 990px;
            }

            @media only screen and (max-width: $bp-md){
                top: 710px;
                width: 208px;
                right: 130px;
            }

            @media only screen and (max-width: $bp-sm){
                top: 730px;
                right: 30px;
            }
        }
    }

    &__stage-number {
        font-family: 'Inter';
        font-weight: 300;
        font-size: 30px;
        line-height: 36px;
        margin-right: 20px;

        @media only screen and (max-width: $bp-xl){
            font-size: 22px;
            margin-right: 10px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 18px;
            margin-right: 0;
            margin-bottom: 6px;
            line-height: 150%;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 14px;
            margin-bottom: 8px;
        }
    }

    &__stage-title {
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 25px;
        line-height: 110%;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-xl){
            font-size: 19px;
            margin-bottom: 18px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 27px;
            margin-bottom: 12px;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 20px;
            margin-bottom: 8px;
        }
    }

    &__stage-text {
        font-family: 'Inter';
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #888;

        @media only screen and (max-width: $bp-xl){
            font-size: 12px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 19px;
            -webkit-line-clamp: 3;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 14px;
        }
    }

    &__main._run &__logo {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
    }

    &__logo {
        width: 344px;
        height: 44px;
        position: absolute;
        top: 365px;
        right: 0;
        z-index: 3;
        transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;

        @media only screen and (min-width: $bp-lg + 1){
            opacity: 0;
            visibility: hidden;
            transform: translateX(-40px);
            transition-delay: 1.4s;
        }


        @media only screen and (max-width: $bp-xl){
            width: 212px;
            height: 27px;
            top: 280px;
        }
        
        @media only screen and (max-width: $bp-lg){
            width: 216px;
            height: 28px;
            left: 100px;
            top: 540px;
        }
        
        @media only screen and (max-width: $bp-md){
            width: 160px;
            height: 21px;
            top: 410px;
            right: auto;
            left: 0;
        }
    }

    &__courses {
        position: absolute;
        top: 1194px;
        width: 100%;

        @media only screen and (max-width: $bp-xl){
            top: 880px;
        }

        @media only screen and (max-width: $bp-lg){
            right: 70px;
            top: 1374px;
            width: 390px;
        }

        @media only screen and (max-width: $bp-md){
            top: 1010px;
            width: 290px;
            right: 50px;
        }

        @media only screen and (max-width: $bp-sm){
            right: -10px;
            top: 1025px;
        }
    }

    &__main._run &__courses-title {
        opacity: 1;
        visibility: visible;
    }

    &__courses-title {
        position: absolute;
        text-align: center;
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 50px;
        line-height: 100%;
        bottom: 125px;
        left: 50%;
        transform: translateX(-50%);
        transition: opacity 0.75s ease, visibility 0.75s ease;

        
        @media only screen and (min-width: $bp-lg + 1){
            opacity: 0;
            visibility: hidden;
            transition-delay: 1.5s;
        }

        @media only screen and (max-width: $bp-xl){
            font-size: 38px;
            bottom: 100px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 54px;
            text-align: left;
            position: static;
            margin-bottom: 95px;
            transform: none;

            br{
                display: none;
            }
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 40px;
            margin-bottom: 40px;
        }
    }

    &__main._run &__course {
        opacity: 1;
        visibility: visible;
        margin-top: 0;
    }

    &__course {
        position: absolute;
        display: flex;
        text-decoration: none;
        color: #000;
        z-index: 3;
        transition: opacity 0.75s ease, visibility 0.75s ease, margin 0.75s ease;

        @media only screen and (min-width: $bp-lg + 1){
            opacity: 0;
            visibility: hidden;
            margin-top: 30px;
        }

        @media only screen and (max-width: $bp-lg){
            align-items: center;
            position: relative !important;
            top: auto !important;
            left: auto !important;
            right: auto !important;
            bottom: auto !important;
            margin-bottom: 56px;

            &:last-of-type{
                margin-bottom: 0;
            }
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 36px;
        }

        &--1{
            left: 0;
            max-width: 466px;
            bottom: 370px;

            @media only screen and (min-width: $bp-lg + 1){
                transition-delay: 2s;
            }

            @media only screen and (max-width: $bp-xl){
                bottom: 280px;
            }
        }


        &--2{
            right: 0px;
            max-width: 412px;
            bottom: 275px;

            @media only screen and (min-width: $bp-lg + 1){
                transition-delay: 2.25s;
            }

            @media only screen and (max-width: $bp-xl){
                bottom: 205px;
                max-width: 306px;
            }
        }

        &--3{
            left: 0;
            max-width: 418px;
            bottom: 60px;

            @media only screen and (min-width: $bp-lg + 1){
                transition-delay: 2.5s;
            }

            @media only screen and (max-width: $bp-xl){
                bottom: 45px;
                max-width: 332px;
            }
        }

        @media only screen and (max-width: $bp-lg){
            max-width: none
        }
    }

    &__course--1 &__course-img::after{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center;
        background-image: url("data:image/svg+xml,%3Csvg width='110' height='110' viewBox='0 0 110 110' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M54.9083 52.2C63.6943 52.2 70.8167 44.7679 70.8167 35.6C70.8167 26.4321 63.6943 19 54.9083 19C46.1224 19 39 26.4321 39 35.6C39 44.7679 46.1224 52.2 54.9083 52.2Z' fill='%2339B8FF'/%3E%3Cpath d='M13 50L53.5 90.5L97.5 46.5' stroke='%233AB3FE' stroke-width='25'/%3E%3C/svg%3E%0A");
        opacity: 0;
        width: 100%;
        height: 100%;
        flex-shrink: 0;
        transition: opacity 0.35s ease;
    }

    
    &__course--1:hover &__course-img::after{
        opacity: 1;
    }

    &__course--2:hover &__course-img{
        transform: rotate(-90deg);
    }

    &__course--3:hover &__course-img{
        transform: rotate(90deg);
    }

    &__course-img {
        position: relative;
        width: 110px;
        height: 110px;
        flex-shrink: 0;
        margin-right: 16px;
        transition: transform 0.35s ease;

        @media only screen and (max-width: $bp-xl){
            width: 70px;
            height: 70px;
            margin-right: 18px;
        }
        
        @media only screen and (max-width: $bp-lg){
            width: 80px;
            height: 80px;
            margin-right: 30px;
        }
        
        @media only screen and (max-width: $bp-md){
            width: 60px;
            height: 60px;
            margin-right: 24px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__course-main {
    }

    &__course-title {
        font-family: 'Raleway';
        font-size: 30px;
        line-height: 120%;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-xl){
            font-size: 22px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 27px;
            margin-bottom: 0;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 20px;
        }
    }

    &__course-labels {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: -10px;
                
        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }

    &__course-label {
        padding: 0 16px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background-color: $blue;
        color: #fff;
        font-size: 16px;
        line-height: 19px;
        margin-right: 12px;
        margin-bottom: 10px;
        height: 35px;
        border-radius: 20px;
        
        @media only screen and (max-width: $bp-xl){
            font-size: 12px;
            height: 24px;
            padding: 0 12px;
        }
        
        &:last-of-type{
            margin-right: 0;
        }
    }
    
    &__course-text{
        margin-top: 16px;
        color: #888888;
        transition: max-height 0.5s ease;
        display: block;
        -webkit-line-clamp: 7;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        max-height: 0;

        @media only screen and (max-width: $bp-xl) {
            -webkit-line-clamp: 5;
        }

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__course:hover &__course-text{
        max-height: 118px;

        @media only screen and (max-width: $bp-xl) {
            max-height: 84px;
        }
    }

    &__bg{
        position: absolute;
        width: auto;
        height: 100%;
        top: 0;
        left: 50%;
        transform: translateX(-50%);

        @media only screen and (max-width: $bp-lg){
            right: 100px;
            left: auto;
            transform: none;
        }

        @media only screen and (max-width: $bp-sm){
            right: -10px;
        }
    }

    &__bg-main{
        width: 1920px;
        height: 2872px;
        height: 2674px;

        @media only screen and (max-width: $bp-xl){
            width: 1409px;
            height: 2108px;
            height: 1962px;
        }

        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }

    &__bg-second{
        display: none;

        @media only screen and (max-width: $bp-lg){
            display: flex;
            height: 100%;
            width: auto;
            position: absolute;
            left: 50%;
            top: 0;
            transform: translateX(-70.5%);
        }
    }


    &__tree{
        position: absolute;
        max-width: 425px;
        color: #000;
        bottom: 185px;
        left: 0;
        opacity: 0;
        visibility: hidden;
        transform: translateY(50px);
        transition: opacity 0.5s ease, visibility 0.5s ease, transform 0.5s ease;
        transition-delay: 0.5s;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }

        &._run{
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
        }
    }

    &__tree-title{
        font-weight: 700;
        font-size: 30px;
        line-height: 130%;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 22px;
            margin-bottom: 10px;
        }
    }

    &__tree-text{
        font-size: 16px;
        line-height: 150%;
        color: #797D80;

        @media only screen and (max-width: $bp-xl) {
            font-size: 12px;
        }
    }

    &__tree-button{
        margin-top: 32px;
    }
}

.path-elements{
    &._run-main &__el{
        &--1{
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
            transition-delay: 1.5s;
        }

        &--2{
            opacity: 1;
            visibility: visible;
            transform: translate(-50%, 0);
            transition-delay: 2s;
        }
    }
    &._run-tree &__el{
        &--3{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }

        &--4{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }

        &--5{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }

        &--6{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }

        &--7{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }

        &--8{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }

        &--9{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }

        &--10{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }

        &--11{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }
        
        &--12{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.5s ease 0.75s, visibility 0.5s ease 0.75s, transform 0.5s ease;
        }
    }
    &__el{
        display: block;
        position: absolute;
        opacity: 0;
        visibility: hidden;
        transition: 0.5s ease;
        background-size: 100%;
        background-repeat: no-repeat;
        z-index: 1;

        @media only screen and (max-width: $bp-lg){
            display: none;
        }

        &--1{
            width: 65px;
            height: 111px;
            top: 340px;
