        transition-delay: 1s;
    }

    &__apple{
        display: block;
        position: absolute;
        z-index: 1;
        width: 69px;
        height: 94px;
        bottom: 610px;
        left: calc(50% + 226px);
        background-size: 100%;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml,%3Csvg width='69' height='94' viewBox='0 0 69 94' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M34.5001 94C15.4463 94 2.94757e-06 78.5538 4.61331e-06 59.5C6.27905e-06 40.4462 15.4463 25 34.5001 25C53.5539 25 69 40.4462 69 59.5C69 78.5538 53.5539 94 34.5001 94Z' fill='%235BBFFE'/%3E%3Cpath d='M58.9307 5.30284C58.9307 5.30284 59.1687 15.6715 52.9203 21.9198C46.6719 28.1682 36.3033 27.9303 36.3033 27.9303C36.3033 27.9303 36.0653 17.5616 42.3137 11.3132C48.5621 5.06486 58.9307 5.30284 58.9307 5.30284Z' fill='%235BBFFE'/%3E%3C/svg%3E%0A");
        opacity: 0;
        visibility: hidden;
        transition: 0.5s ease;

        @media only screen and (max-width: $bp-xl){
            width: 50px;
            height: 68px;
            bottom: 445px;
            left: calc(50% + 165px);
        }

        &._run{
            animation: appleFall 1s linear forwards;
        }

        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }

    &__apple-text{
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.45s ease, visibility 0.45s ease;
        position: absolute;
        left: calc(50% + 276px);
        top: calc(100% + 20px);
        color: $blue-dark;

        @media only screen and (max-width: $bp-xl){
            left: calc(50% + 132px);
        }

        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }
    
    &__apple._run + &__apple-text{
        opacity: 1;
        visibility: visible;
        transition-delay: 1.25s;
    }
}

@keyframes appleFall {
    0%{
        bottom: 610px;
    }

    40%{
        bottom: 400px;
        transform: rotate(0);
        left: calc(50% + 226px);

    }
    75%{
        bottom: 255px;
        left: calc(50% + 390px);
        transform: rotate(90deg);
    }

    100%{
        bottom: 2px;
        left: calc(50% + 508px);
        transform: rotate(250deg);
    }
}

@media only screen and (max-width: $bp-xl){
    @keyframes appleFall {
        0%{
            bottom: 445px;
        }
    
        40%{
            bottom: 292px;
            transform: rotate(0);
            left: calc(50% + 165px);
    
        }
        75%{
            bottom: 186px;
            left: calc(50% + 285px);
            transform: rotate(90deg);
        }
    
        100%{
            bottom: 2px;
            left: calc(50% + 370px);
            transform: rotate(250deg);
        }
    }
}